<template>

    <el-menu
            default-active="2"
            class="el-menu-vertical-demo"
            :collapse="isCollapse"
            @open="handleOpen"
            style="height: 100vh">
        <el-row justify="center" style="align-items: center;height: 70px;line-height: 70px;">
            <img src="../../../assets/loginImg.png" style="height: 40px;width: 40px;border-radius: 50%;float: left;margin-top: 13px;margin-left: 10px;margin-right: 5px" alt="logo">
            <h3 v-show="!isCollapse" style="margin-left: 10px;width: 160px"></h3>
        </el-row>

        <el-submenu index="1" v-show="!adminIs">
            <template slot="title">
                <i class="el-icon-user"></i>
                <span></span>
            </template>
            <el-menu-item-group>
                <el-menu-item index="1-2" @click="updatePage('userList')"></el-menu-item>
            </el-menu-item-group>
        </el-submenu>

        <el-submenu index="3">
            <template slot="title">
                <i class="el-icon-paperclip"></i>
                <span></span>
            </template>
            <el-menu-item-group>
                <el-menu-item index="3-1" @click="updatePage('orderTypeAdd')"></el-menu-item>
                <el-menu-item index="3-2" @click="updatePage('orderTypeList')"></el-menu-item>
            </el-menu-item-group>
        </el-submenu>

        <el-submenu index="4">
            <template slot="title">
                <i class="el-icon-paperclip"></i>
                <span></span>
            </template>
            <el-menu-item-group>
                <el-menu-item index="4-1" @click="updatePage('orderTypeSonAdd')"></el-menu-item>
                <el-menu-item index="4-2" @click="updatePage('orderTypeSonList')"></el-menu-item>
            </el-menu-item-group>
        </el-submenu>

        <el-submenu index="2">
            <template slot="title">
                <i class="el-icon-document-copy"></i>
                <span></span>
            </template>
            <el-menu-item-group>
                <el-menu-item index="2-1" @click="updatePage('orderList')"></el-menu-item>
            </el-menu-item-group>
        </el-submenu>

        <el-submenu index="5">
            <template slot="title">
                <i class="el-icon-document-checked"></i>
                <span></span>
            </template>
            <el-menu-item-group>
                <el-menu-item index="5-1" @click="updatePage('receiveList')"></el-menu-item>
            </el-menu-item-group>
        </el-submenu>

        <el-submenu index="6">
            <template slot="title">
                <i class="el-icon-chat-line-round"></i>
                <span></span>
            </template>
            <el-menu-item-group>
                <el-menu-item index="6-1" @click="updatePage('commentList')"></el-menu-item>
            </el-menu-item-group>
        </el-submenu>

        <el-submenu index="7" v-show="admin.aRole==='root'">
            <template slot="title">
                <i class="el-icon-aim"></i>
                <span></span>
            </template>
            <el-menu-item-group>
                <el-menu-item index="7-1" @click="updatePage('applyList')"></el-menu-item>
            </el-menu-item-group>
        </el-submenu>

        <el-submenu index="8" v-show="admin.aRole==='root'">
            <template slot="title">
                <i class="el-icon-s-custom"></i>
                <span></span>
            </template>
            <el-menu-item-group>
                <el-menu-item index="8-1" @click="updatePage('adminAdd')"></el-menu-item>
                <el-menu-item index="8-2" @click="updatePage('adminList')"></el-menu-item>
            </el-menu-item-group>
        </el-submenu>
    </el-menu>

</template>

<script>
    export default {
        name: "left",
        props: ['isCollapse','adminIs'],
        data() {
            return {
                admin:{}
            }
        },
        methods:{
            handleOpen(key, keyPath) {
                console.log(key, keyPath);
            },
            updatePage(page){
                this.$emit("updatePage",page);
            }
        },
        mounted() {
            console.log("left");
            let admin=this.$mySetToken();
            if (admin==null){
                this.$message.error("");
                this.$router.push({path:'/login',query:{}});
                return
            }
            console.log(admin)
            this.admin=admin;
        }
    }
</script>

