<template>
    <div v-loading="loading">
        <div style="line-height: 40px;">
            <h3 style="float: left;margin-right: 20px;"></h3>
            <el-button type="primary" @click="initDate"></el-button>
        </div>

        <div v-if="dialogVisible">
            <el-dialog
                    title=""
                    :visible.sync="dialogVisible"
                    width="700px"
                    :before-close="handleClose">
                <edit v-bind:oid="oid"></edit>
                <span slot="footer" class="dialog-footer">
            </span>
            </el-dialog>
        </div>

        <el-table
                :data="tableData"
                style="width: 100%">

            <el-table-column
                    prop="oid"
                    label="ID"
                    width="100">
            </el-table-column>

            <el-table-column
                    prop="oimage"
                    label=""
                    width="200">
                <template slot-scope="scope">
                    <img :src="scope.row.oimage" width="120" height="100%" class="head_pic"/>
                </template>
            </el-table-column>

            <el-table-column
                    label=""
                    width="120">
                <template slot-scope="scope">
                    <el-popover trigger="hover" placement="top">
                        <p>: {{ scope.row.otile }}</p>
                        <p>: {{ scope.row.odescribe }}</p>
                        <div slot="reference" class="name-wrapper">
                            <el-tag size="medium">{{ scope.row.otile }}</el-tag>
                        </div>
                    </el-popover>
                </template>
            </el-table-column>

            <el-table-column
                    label=""
                    width="100">
                <template slot-scope="scope">
                    <span v-if="scope.row.ostate===0" style="color: red"></span>
                    <span v-if="scope.row.ostate===1" style="color:green;"></span>
                    <span v-if="scope.row.ostate===2" style="color:blue;"></span>
                    <span v-if="scope.row.ostate===3" style="color: orange"></span>
                </template>
            </el-table-column>

            <el-table-column
                    prop="orderType.otName"
                    label=""
                    width="100">
            </el-table-column>

            <el-table-column
                    prop="orderTypeSon.tsName"
                    label=""
                    width="100">
            </el-table-column>

            <el-table-column
                    prop="ocreateTime"
                    label=""
                    width="260">
                <template slot-scope="scope">
                    <el-date-picker
                            readonly="true"
                            v-model="scope.row.ocreateTime"
                            type="datetime"
                            placeholder="">
                    </el-date-picker>
                </template>
            </el-table-column>

            <el-table-column
                    prop="oendTime"
                    label=""
                    width="260">
                <template slot-scope="scope">
                    <el-date-picker
                            readonly="true"
                            v-model="scope.row.oendTime"
                            type="datetime"
                            placeholder="">
                    </el-date-picker>
                </template>
            </el-table-column>

            <el-table-column
                    label=""
                    width="180">
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                            @click="handleEdit(scope.$index, scope.row)">
                        <i class="el-icon-edit"></i>
                        </el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.$index, scope.row)">
                        <i class="el-icon-delete"></i>
                        </el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    import edit from "./edit";
    export default {
        components:{edit},
        data() {
            return {
                oid:'',
                dialogVisible:false,
                keyword:"",
                loading:true,
                tableData: []
            }
        },
        methods: {
            handleClose(done) {//
                let _this=this;
                console.log(_this)
                done()
                _this.initDate()
            },
            handleEdit(index, row) {//
                console.log(index, row);
                this.oid=row.oid;
                this.dialogVisible=true;
            },
            handleDelete(index, row) {//
                console.log(index, row);
                let _this=this;
                this.$http.delete('/order',{
                    params:{
                        id:row.oid
                    }
                }).then(function (res) {
                    _this.$myRequest(res);//
                    if (res.data.code===200){
                        _this.initDate();//
                        _this.$message.success(res.data.message);
                    }
                });
            },
            initDate(){//
                let _this=this;
                this.$http.get("/order",{
                    params:{
                        page:0,
                        size:10000
                    }
                }).then(function (res) {
                    _this.$myRequest(res);//
                    _this.tableData=res.data.data.body.content;
                });
            }
        },
        mounted() {
            let admin=this.$mySetToken();//
            this.initDate();//
        }
    }
</script>
